<template>
    <div>
        <h1></h1>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" style="width:800px" class="demo-ruleForm">
            <el-form-item label="" prop="article_name">
                <el-input v-model="ruleForm.article_name"></el-input>
            </el-form-item>
            <el-form-item label="" prop="oneId">
                <el-select v-model="ruleForm.oneId" placeholder=""  @change="changeClassOne()">
                    <el-option v-for="items in oneClass" :key="items.id" :label="items.cnname" :value="items.id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="twoId">
                <el-select v-model="ruleForm.twoId" placeholder="" :clearable="true"> 
                    <el-option v-for="item in twoClass" :label="item.cnname" :value="item.id" :key="item.id"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="art_show">
                <el-radio-group v-model="ruleForm.art_show">
                    <el-radio label="1"></el-radio>
                    <el-radio label="0" is-checked></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="" prop="recommend">
                <el-radio-group v-model="ruleForm.recommend">
                    <el-radio label="1"></el-radio>
                    <el-radio label="0" is-checked></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="" prop="editer" style="width:600px">
                <el-input v-model="ruleForm.editer"></el-input>
            </el-form-item>
            <el-form-item label="" style="width:800px" prop="daodu">
                <el-input type="textarea" :row="2" :autosize='{minRows:2,maxRows:10}' v-model="ruleForm.daodu" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" class="block" prop="time">
                <el-date-picker v-model="ruleForm.time" type="datetime" placeholder="" class="data" @change="timefn" format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="content">
                <!-- <el-col :span="18" :offset="0"> -->
                    <div id="editor"></div>
                <!-- </el-col> -->
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>
        
    </div>
</template>
<style scoped>
h1 {
    font-size: 18px;
    font-weight: bold;
    padding-left: 20px;
    line-height: 50px;
}

.el-input {
    width: 600px;
}

.block {
    margin-bottom: 20px;
}

.demonstration {
    font-size: 14px;
    margin-right: 8px;
}

.data {
    width: 217px;
}

#edui1_iframeholder {
    height: 100px;
}
</style>
<script>
export default {
    data() {
        return {
            editor:null,
            oneClass:[],
            twoClass:[],
            twoClasAll:[],
            oneClassDate:{},
            ruleForm: {
                article_name: '',
                oneId: '',
                twoId: '',
                art_show: "0",
                recommend: "0",
                editer: '',
                daodu: '',
                time: '',
                content: '',
               imgsrc:'',
                enname_one:''
            },
            rules: {
                article_name: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 0, max: 10, message: ' 0  10 ', trigger: 'blur' }
                ],
                oneId: [
                    { required: true, message: '' }
                ],
                twoId: [
                    { required: true, message: '' }
                ],
                 times: [
                    { required: true, message: '', }
                ],
                editer: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 0, max: 10, message: ' 0  10 ', trigger: 'blur' }
                ],
                daodu: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                content: [
                    { required: false, message: '', trigger: 'blur' }
                ]
            },
            editor: null,

        }
    },
    created(){
        // var _this = this;
        this.axios.get("/api/back_article/getClass").then((data)=>{
            this.oneClass = data.data.data[0];
            this.twoClasAll = data.data.data[1]
            console.log(this.oneClass)
        },this)
    },
    methods: {
        timefn(timedata){
            console.log(timedata)
            this.ruleForm.time = timedata
        },
        changeClassOne(){
            this.twoClass = [];
            var oneItem = null;
            this.oneClass.forEach(function(i) {
                if(i.id == this.ruleForm.oneId){
                    oneItem = i;
                   
                }
            }, this);
             console.log(oneItem.enname)
            this.twoClasAll.forEach(function(i,index){
                if(i.parent_id == this.ruleForm.oneId){
                    this.twoClass.push(i)
                }
            },this)
            this.ruleForm.twoId = "";
            this.ruleForm.enname_one = oneItem.enname;
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
            console.log(this.ruleForm)
                    this.axios.post('/api/back_article/addArticle', this.ruleForm)
                        .then(function(response) {
                            console.log(response)
                            switch(response.data.code){
                                case "3010" : 
                                // alert("")
                                this.$message({
                                    showClose:true,
                                    message:"",
                                    type:"error",
                                })
                                break;
                               case "3011" :
                               this.$message({
                                   showClose:true,
                                   message:"",
                                   type:"success",
                                   
                               })
                                break;
                            }
                        })
                        .catch(function(error) {
                            console.log(error);
                        });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
            // console.log(this.ruleForm)

        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },

    },
    mounted() {
        // editor
        this.editor = UE.getEditor('editor');
        // console.log(this.editor.getContent())
    },
    destroyed() {
        // editor
        this.editor.destroy();
    }
}
</script>



