<template>
    <div>

        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <h1></h1>

            <el-form-item label="" prop="title">
                <el-input style="width:200px" v-model="ruleForm.title"></el-input>
            </el-form-item>
            <el-form-item label="" prop="url">
                <el-input style="width:200px" v-model="ruleForm.url"></el-input>
            </el-form-item>
            <el-form-item label="" prop="backorfont">
                <el-select v-model="ruleForm.backorfont" placeholder="">
                    <el-option label="" value=""></el-option>
                    <el-option label="" value=""></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="type">
                <el-select v-model="ruleForm.type" placeholder="">
                    <el-option label="get" value="get"></el-option>
                    <el-option label="post" value="post"></el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="" prop="sendparams">
                <el-input type="textarea" v-model="ruleForm.sendparams"></el-input>
            </el-form-item>
            <el-form-item label="" prop="getparams">
                <el-input type="textarea" v-model="ruleForm.getparams"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<style scoped>
h1 {
    font-size: 18px;
    font-weight: bold;
    padding-left: 20px;
    line-height: 50px;
}
</style>
<script>
export default {
    data() {
        return {
            ruleForm: {
                title:"",  //
                url:"",  //
                type:"", //
                sendparams:"", //
                getparams:"", //
                backorfont:"", //
            },
            rules: {
                title: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                url: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                backorfont: [
                    { required: true, message: '', trigger: 'change' }
                ],
                type: [
                    { required: true, message: '', trigger: 'change' }
                ],
                sendparams: [
                    { required: true, message: '', trigger: 'blur' }
                ],
                getparams: [
                    { required: true, message: '', trigger: 'blur' }
                ]

            }
        }
    },
    
    methods:{
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
            console.log(this.ruleForm)
            var _this = this;
                    this.axios.post('/api/apilist/', this.ruleForm)
                        .then(function(response) {
                            switch(response.data.code){
                                case "2001" : 
                                // alert("")
                                _this.$message({
                                    showClose:true,
                                    message:"",
                                    type:"error",
                                })
                                break;
                               case "2000" :
                               _this.$message({
                                   showClose:true,
                                   message:"",
                                   type:"success" 
                               })
                                break;
                            }
                        })
                        .catch(function(error) {
                            console.log(error);
                        });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
}
</script>



