<template>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
        <h1></h1>
        <el-form-item label="" prop="region">
            <el-select v-model="ruleForm.oneId" placeholder="">
                <el-option v-for="items in oneClass" :key="items.id" :label="items.cnname" :value="items.id"></el-option>
            </el-select>
        </el-form-item>
        <h1></h1>
        <el-form-item label="" prop="cnname_two">
            <el-input style="width:200px"  v-model="ruleForm.cnname_two"></el-input>
        </el-form-item>
        <el-form-item label="" prop="enname_two">
            <el-input style="width:200px" v-model="ruleForm.enname_two"></el-input>
        </el-form-item>
        <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
    </el-form>
</template>
<style scoped>
h1 {
    font-size: 18px;
    font-weight: bold;
    padding-left: 20px;
    line-height: 50px;
}
</style>
<script>
export default {
    data() {
         var englishTest = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else {
                if (!/^[a-zA-Z]{3,15}$/.test(value)) {
                   callback(new Error(''));
                }
                callback();
            }
        };
        return {
            oneClass: [],
            ruleForm: {
                oneId: "",
                enname_two: "",
                cnname_two:"",
            },
            rules: {
                enname_two: [
                    { validator: englishTest, trigger: 'blur' }
                ]
            }
        }
    },
    created() {
        this.axios.get("/api/back_article/getClass").then((data) => {
            this.oneClass = data.data.data[0];
        }, this)
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    var _this = this;
                    this.axios.post('/api/back_class/add_two_class', this.ruleForm)
                        .then(function(response) {
                            switch (response.data.code) {
                                case "1030":
                                    _this.$message({
                                        type: "error",
                                        message: ""
                                    })
                                    break;
                                case "1031":
                                    _this.$message({
                                        type: "error",
                                        message: ""
                                    })
                                    break;
                                case "1032":
                                    _this.$message({
                                        type: "success",
                                        message: ""
                                    })
                                    break;
                            }
                        })
                        .catch(function(error) {
                            console.log(error);
                        });
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
    }
}
</script>



