<template>
    <div>
        <el-table class="tab" :data="formData" style="width: 900px;margin-left:20px;margin-top:20px" :row-class-name="tableRowClassName"
         @row-dblclick="dbclick">
            <el-table-column prop="title" label="">
            </el-table-column>
            <el-table-column prop="backorfont" label="" width="180">
            </el-table-column>
            <el-table-column prop="time" label="" width="180">
            </el-table-column>
            <el-table-column label="">
                <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="nowpage" :page-sizes="[2,5,10,15,20]" :page-size="everyrows" layout="total, sizes, prev, pager, next, jumper" :total="oldData.length">
        </el-pagination>
        <el-dialog title="" :visible.sync="dialogVisible" size="tiny" :before-close="handleClose">
            <span></span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false"> </el-button>
                <el-button type="primary" @click="handleOk"> </el-button>
            </span>
        </el-dialog>
        
    </div>
</template>
<style scoped>
h1 {
    font-size: 18px;
    font-weight: bold;
    padding-left: 20px;
    line-height: 50px;
}

.el-table .info-row {
    background: #c9e5f5;
}

.el-table .positive-row {
    background: #e2f0e4;
}

.pagination {
    text-align: center;
    width: 900px;
    margin-top: 20px;
}
</style>
<script>
export default {
    data() {
        return {
            nowpage: 5,
            dialogVisible: false,
            formData: [],
            everyrows:2,
            oldData:[],
            id:""
        }
    },
     created(){
       this.getData();
    },
    methods: {
        getData(){
            this.axios.get("/api/apilist/list?type=back").then((data)=>{
            if(data.data.code=="2010"){
                this.oldData=data.data.data;
                console.log(data)
                this.formData=this.oldData.slice(0,this.everyrows)
            }        
            })
        },
      dbclick(row){
        this.$router.push({path:"/Hello/api_list_detail",query:{data:row}})
      },
      handleSizeChange(val) {
        this.everyrows=val;
        this.formData=this.oldData.slice((this.nowpage-1)*this.everyrows,this.nowpage*this.everyrows)
      },
      handleCurrentChange(val) {
        this.nowpage=val;
        this.formData=this.oldData.slice((val-1)*this.everyrows,val*this.everyrows)
      },
        tableRowClassName(row, index) {
            if (index === 1) {
                return 'info-row';
            } else if (index === 3) {
                return 'positive-row';
            }
            return '';
        },
        handleEdit(index,data){
            this.$router.push({name:"apiAmend",query:{data:data}})
        },
         handleDelete: function(index, data) {
            this.dialogVisible = true;
            this.deteleId = data.id
        },
        handleOk() {
            var _this = this;
            this.dialogVisible = false;
            var _this = this;
            this.axios.post("/api/apilist/delete",{
                id: _this.deteleId   //id
            }).then((response)=>{
                switch(response.data.code){
                    case "2032":
                        this.$message({
                                message: '',
                                type: 'success'
                            })
                    break;
                    case "2031":
                        this.$message({
                                message: '',
                                type: 'error'
                            })
                    break;
                }
            })
        },
        handlerror() {
            this.dialogVisible = false;
        },
        handleClose(done) {
            this.$confirm('')
                .then(_ => {
                    done();
                })
                .catch(_ => { });
        }

    }
}
</script>



